@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400&display=swap);
.landing_landingContainer__FyGkO {
  width: 100%; 
  margin: unset;
}
.landing_landingBanner__3_37a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 75vh;
  background-image: url(https://cdn.forbes.co/2020/05/Agro-scaled.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.landing_bankLogo__3_nMv {
  margin-bottom: 2rem;
}
.footerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: rgb(42, 42, 42);
}
.footerColumn {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 33%;
  padding: 15px 5px
}
.footerTitle, .footerList, .footerColumn p {
  color: white;
  font-family: "Roboto", sans-serif;
  list-style-type: none;
  margin: 0;
}
.footerTitle {
  font-size: calc(100vw * (24 / 1366));
  font-weight: 900;
  text-decoration: none;
  margin-bottom: 15px;
}
.footerList li {
  font-size: calc(100vw * (16 / 1366));
}
.footerList li a {
  text-decoration: none;
  color: white;
}
.footerList li a:hover {
  color: green;
  text-decoration: underline;
}
.bankLogoFooter {
  width: 55px;
  height: 55px;
}
.footerColumn p {
  font-size: calc(100vw * (14 / 1366));
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.registro_form__2z6uC{
    text-align: left;
    width: 97%;
    max-width: 500px;
    border: 1px solid #ccc; 
    border-radius: 10px; 
    box-shadow: 3px 3px 3px rgba(0, 0, 0,.2);
    margin: 20px auto;
    padding: 15px;
}

p{
    font-size: 12px;
    color: #ff0000;
    margin-bottom: 0;
}
.modal {
	position: fixed;
	z-index: 999;
	top: 0;
	left: 0;
	width: 100vw;
	min-height: 100vh;
	background-color: rgba(0,0,0,.75);
	display: none;
	justify-content: center;
	align-items: center;
}

.modal.is-open {
	display: flex;
}

.modal-container {
	position: relative;
	padding: 0.75rem;
	border-radius: 8px;
	min-width: 320px;
	max-width: 480px;
	min-height: 200px;
	max-height: 600px;
	overflow-y: auto;
	background-color: #fff;
}

.modal-close {
	position: absolute;
	font-size: 24px;
	top: 1rem;
	right: 1rem;
	padding: 0.1rem 0.3rem;
	background-color: transparent;
	border: none;
}
i{
    font-size: 20px;
}
.tableSide_efecto__1m50n,.tableSide_enlace__3AQU7{
    border-radius: 4px;
}
.tableSide_efecto__1m50n:hover{
    background: rgba(255, 255, 255, .3);
}

.homePanel_imagen__2t_e3{
    width: 100%;
    height: 100%;
    position: relative;
}

.homePanel_titulo__1pZNJ{
    position: absolute;
    top: 20%;
    text-shadow: 5px 5px 5px rgb(105, 104, 104);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}
